
import { mapGetters, mapMutations } from 'vuex'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'add-address',
data() {
return {
form: {
name: '',
mobile: '',
cityArr: [],
detail: ''
},
addressData: ChinaAddressV4Data
}
},
created() {
if (this.userInfo.addressInfo) {
this.form = {
name: this.userInfo.addressInfo.name,
mobile: this.userInfo.addressInfo.mobile,
cityArr: this.userInfo.addressInfo.cityArr,
detail: this.userInfo.addressInfo.detail
}
}
},
computed: {
...mapGetters(['userInfo'])
},
methods: {
...mapMutations(['UPDATE_ADDRESS']),
submitAddress() {
const { name, mobile, cityArr, detail } = this.form
if (!name) {
this.$vux.toast.text('请输入名字~', 'middle')
return
}
if (!mobile) {
this.$vux.toast.text('请输入手机号~', 'middle')
return
}
if (!cityArr) {
this.$vux.toast.text('请选择地区~', 'middle')
return
}
if (!detail) {
this.$vux.toast.text('请输入详情地址~', 'middle')
return
}
let cityVal = value2name(cityArr, ChinaAddressV4Data)
this.UPDATE_ADDRESS({
name: name,
mobile: mobile,
cityArr: cityArr,
cityVal: cityVal,
detail: detail
})
this.$router.go(-1)
}
},
components: {
XTextarea,
Group,
XInput,
XAddress,
XButton,
Cell
}
}



